@import url(https://fonts.googleapis.com/css2?family=Roboto:wght@300&display=swap);
*{font-family:"Roboto", sans-serif;zoom:1.1}.buttons{display:flex;justify-content:space-around}.App__title{display:flex;justify-content:center;padding:10px 0 20px}.container{display:flex;flex-direction:column;max-width:500px;margin:0 auto;padding:20px 0;background-color:cornflowerblue;border:2px solid blue;border-radius:10px}.button{border-radius:10px;box-shadow:2px 2px 2px 1px rgba(0,0,255,0.2);cursor:pointer;transition:-webkit-transform 0.2s linear;transition:transform 0.2s linear;transition:transform 0.2s linear, -webkit-transform 0.2s linear}.button:hover{-webkit-transform:scale(1.05);transform:scale(1.05)}.App__add-one{background-color:#ffbd33;border:1.5px solid orange}.App__add-100{background-color:#dbff33;border:1.5px solid #d6d600}.App__increase{background-color:#75ff33;border:1.5px solid #89e400}

